import { ref, readonly } from 'vue'

interface FeishuSelection {
  baseId: string | null
  tableId: string | null
  fieldId: string | null
  viewId: string | null
  recordId: string | null
}

interface FeishuField {
  fieldId: string
  fieldName: string
  fieldType: string
}

interface FeishuRecord {
  recordId: string
  fields: Record<string, any>
}

export const useFeishu = () => {
  const isFeishuEnv = ref(false)
  const selection = ref<FeishuSelection | null>(null)
  const loading = ref(false)
  const error = ref<string | null>(null)

  // æ£€æŸ¥æ˜¯å¦åœ¨é£ä¹¦ç¯å¢ƒä¸­
  const checkFeishuEnvironment = () => {
    // æ£€æŸ¥æ˜¯å¦æœ‰é£ä¹¦SDK
    if (typeof window !== 'undefined') {
      // æ£€æŸ¥æ˜¯å¦åœ¨iframeä¸­ï¼ˆé£ä¹¦æ’ä»¶ç¯å¢ƒï¼‰
      const inIframe = window.self !== window.top
      // æ£€æŸ¥URLæ˜¯å¦åŒ…å«é£ä¹¦ç›¸å…³å‚æ•°
      const hasFeishuParams = window.location.search.includes('app_token') || 
                             window.location.search.includes('table_id')
      
      isFeishuEnv.value = inIframe || hasFeishuParams
    }
    return isFeishuEnv.value
  }

  // åˆå§‹åŒ–é£ä¹¦SDK
  const initFeishuSDK = async () => {
    if (typeof window === 'undefined') return false
    
    try {
      // åŠ¨æ€å¯¼å…¥é£ä¹¦SDK
      const { bitable } = await import('@lark-base-open/js-sdk')
      
      // æ£€æŸ¥SDKæ˜¯å¦å¯ç”¨
      if (!bitable) {
        throw new Error('é£ä¹¦SDKä¸å¯ç”¨')
      }

      return bitable
    } catch (err) {
      console.warn('é£ä¹¦SDKåˆå§‹åŒ–å¤±è´¥:', err)
      error.value = 'é£ä¹¦SDKåˆå§‹åŒ–å¤±è´¥'
      return null
    }
  }

  // è·å–å½“å‰é€‰æ‹©çš„è¡¨æ ¼ä¿¡æ¯
  const getCurrentSelection = async () => {
    loading.value = true
    error.value = null

    try {
      const bitable = await initFeishuSDK()
      if (!bitable) {
        throw new Error('é£ä¹¦SDKä¸å¯ç”¨')
      }

      const currentSelection = await bitable.base.getSelection()
      selection.value = currentSelection
      
      return currentSelection
    } catch (err) {
      console.error('è·å–é€‰æ‹©ä¿¡æ¯å¤±è´¥:', err)
      error.value = err instanceof Error ? err.message : 'è·å–é€‰æ‹©ä¿¡æ¯å¤±è´¥'
      return null
    } finally {
      loading.value = false
    }
  }

  // è·å–è¡¨æ ¼å­—æ®µä¿¡æ¯
  const getTableFields = async (tableId?: string) => {
    loading.value = true
    error.value = null

    try {
      const bitable = await initFeishuSDK()
      if (!bitable) {
        throw new Error('é£ä¹¦SDKä¸å¯ç”¨')
      }

      let table
      if (tableId) {
        table = await bitable.base.getTable(tableId)
      } else {
        table = await bitable.base.getActiveTable()
      }

      const fieldList = await table.getFieldList()
      const fields: FeishuField[] = []

      for (const field of fieldList) {
        const fieldMeta = await field.getMeta()
        fields.push({
          fieldId: fieldMeta.id,
          fieldName: fieldMeta.name,
          fieldType: fieldMeta.type
        })
      }

      return fields
    } catch (err) {
      console.error('è·å–è¡¨æ ¼å­—æ®µå¤±è´¥:', err)
      error.value = err instanceof Error ? err.message : 'è·å–è¡¨æ ¼å­—æ®µå¤±è´¥'
      return []
    } finally {
      loading.value = false
    }
  }

  // è·å–è¡¨æ ¼è®°å½•
  const getTableRecords = async (tableId?: string, viewId?: string) => {
    loading.value = true
    error.value = null

    try {
      const bitable = await initFeishuSDK()
      if (!bitable) {
        throw new Error('é£ä¹¦SDKä¸å¯ç”¨')
      }

      let table
      if (tableId) {
        table = await bitable.base.getTable(tableId)
      } else {
        table = await bitable.base.getActiveTable()
      }

      const recordList = await table.getRecords({
        pageSize: 100, // é™åˆ¶æ¯æ¬¡è·å–çš„è®°å½•æ•°
        viewId: viewId
      })

      const records: FeishuRecord[] = recordList.records.map(record => ({
        recordId: record.recordId,
        fields: record.fields
      }))

      return records
    } catch (err) {
      console.error('è·å–è¡¨æ ¼è®°å½•å¤±è´¥:', err)
      error.value = err instanceof Error ? err.message : 'è·å–è¡¨æ ¼è®°å½•å¤±è´¥'
      return []
    } finally {
      loading.value = false
    }
  }

  // æ›´æ–°è®°å½•
  const updateRecord = async (recordId: string, fields: Record<string, any>, tableId?: string) => {
    loading.value = true
    error.value = null

    try {
      const bitable = await initFeishuSDK()
      if (!bitable) {
        throw new Error('é£ä¹¦SDKä¸å¯ç”¨')
      }

      let table
      if (tableId) {
        table = await bitable.base.getTable(tableId)
      } else {
        table = await bitable.base.getActiveTable()
      }

      await table.setRecord(recordId, fields)
      return true
    } catch (err) {
      console.error('æ›´æ–°è®°å½•å¤±è´¥:', err)
      error.value = err instanceof Error ? err.message : 'æ›´æ–°è®°å½•å¤±è´¥'
      return false
    } finally {
      loading.value = false
    }
  }

  // æ‰¹é‡æ›´æ–°è®°å½•
  const batchUpdateRecords = async (updates: Array<{recordId: string, fields: Record<string, any>}>, tableId?: string) => {
    loading.value = true
    error.value = null

    try {
      const bitable = await initFeishuSDK()
      if (!bitable) {
        throw new Error('é£ä¹¦SDKä¸å¯ç”¨')
      }

      let table
      if (tableId) {
        table = await bitable.base.getTable(tableId)
      } else {
        table = await bitable.base.getActiveTable()
      }

      const results = []
      for (const update of updates) {
        try {
          await table.setRecord(update.recordId, update.fields)
          results.push({ success: true, recordId: update.recordId })
        } catch (err) {
          results.push({ 
            success: false, 
            recordId: update.recordId, 
            error: err instanceof Error ? err.message : 'æ›´æ–°å¤±è´¥' 
          })
        }
      }

      return results
    } catch (err) {
      console.error('æ‰¹é‡æ›´æ–°è®°å½•å¤±è´¥:', err)
      error.value = err instanceof Error ? err.message : 'æ‰¹é‡æ›´æ–°è®°å½•å¤±è´¥'
      return []
    } finally {
      loading.value = false
    }
  }

  // åˆ›å»ºæ–°å­—æ®µ
  const createField = async (fieldName: string, fieldType: string, tableId?: string) => {
    loading.value = true
    error.value = null

    try {
      const bitable = await initFeishuSDK()
      if (!bitable) {
        throw new Error('é£ä¹¦SDKä¸å¯ç”¨')
      }

      let table
      if (tableId) {
        table = await bitable.base.getTable(tableId)
      } else {
        table = await bitable.base.getActiveTable()
      }

      const field = await table.addField({
        type: fieldType,
        name: fieldName
      })

      return field
    } catch (err) {
      console.error('åˆ›å»ºå­—æ®µå¤±è´¥:', err)
      error.value = err instanceof Error ? err.message : 'åˆ›å»ºå­—æ®µå¤±è´¥'
      return null
    } finally {
      loading.value = false
    }
  }

  // ä»URLå‚æ•°è·å–baseIdå’ŒtableIdï¼ˆå¤‡ç”¨æ–¹æ¡ˆï¼‰
  const getIdsFromUrl = () => {
    if (typeof window === 'undefined') return { baseId: null, tableId: null }
    
    const urlParams = new URLSearchParams(window.location.search)
    return {
      baseId: urlParams.get('app_token') || urlParams.get('base_id'),
      tableId: urlParams.get('table_id')
    }
  }

  // è‡ªåŠ¨è·å–è¡¨æ ¼ä¿¡æ¯
  const autoGetTableInfo = async () => {
    // é¦–å…ˆæ£€æŸ¥æ˜¯å¦åœ¨é£ä¹¦ç¯å¢ƒ
    if (!checkFeishuEnvironment()) {
      // å¦‚æœä¸åœ¨é£ä¹¦ç¯å¢ƒï¼Œå°è¯•ä»URLå‚æ•°è·å–
      const urlIds = getIdsFromUrl()
      if (urlIds.baseId && urlIds.tableId) {
        return {
          baseId: urlIds.baseId,
          tableId: urlIds.tableId,
          source: 'url'
        }
      }
      
      error.value = 'è¯·åœ¨é£ä¹¦å¤šç»´è¡¨æ ¼ç¯å¢ƒä¸­ä½¿ç”¨æ­¤åŠŸèƒ½'
      return null
    }

    // åœ¨é£ä¹¦ç¯å¢ƒä¸­ï¼Œä½¿ç”¨SDKè·å–
    const currentSelection = await getCurrentSelection()
    if (currentSelection && currentSelection.baseId && currentSelection.tableId) {
      return {
        baseId: currentSelection.baseId,
        tableId: currentSelection.tableId,
        source: 'sdk'
      }
    }

    error.value = 'æ— æ³•è·å–è¡¨æ ¼ä¿¡æ¯ï¼Œè¯·ç¡®ä¿å·²é€‰æ‹©è¡¨æ ¼'
    return null
  }

  return {
    // çŠ¶æ€
    isFeishuEnv: readonly(isFeishuEnv),
    selection: readonly(selection),
    loading: readonly(loading),
    error: readonly(error),
    
    // æ–¹æ³•
    checkFeishuEnvironment,
    initFeishuSDK,
    getCurrentSelection,
    getTableFields,
    getTableRecords,
    updateRecord,
    batchUpdateRecords,
    createField,
    getIdsFromUrl,
    autoGetTableInfo
  }
}
